// Utilitrios para validao de API Keys
export const validateApiKey = (provider, apiKey) => {
  if (!apiKey || apiKey.trim().length === 0) {
    return { valid: false, message: 'API Key no pode estar vazia' };
  }

  const providerConfig = AI_PROVIDERS[provider];
  if (!providerConfig) {
    return { valid: false, message: 'Provedor no suportado' };
  }

  // const keyFormat = providerConfig.keyFormat;
  
  // Validaes bsicas baseadas no formato esperado
  switch (provider) {
    case 'openai':
      if (!apiKey.startsWith('sk-')) {
        return { valid: false, message: 'API Key da OpenAI deve comear com "sk-"' };
      }
      if (apiKey.length < 20) {
        return { valid: false, message: 'API Key da OpenAI muito curta' };
      }
      break;
      
    case 'anthropic':
      if (!apiKey.startsWith('sk-ant-')) {
        return { valid: false, message: 'API Key da Anthropic deve comear com "sk-ant-"' };
      }
      break;
      
    case 'google':
      if (!apiKey.startsWith('AI')) {
        return { valid: false, message: 'API Key do Google AI deve comear com "AI"' };
      }
      break;
      
    case 'groq':
      if (!apiKey.startsWith('gsk_')) {
        return { valid: false, message: 'API Key da Groq deve comear com "gsk_"' };
      }
      break;
    default:
      break;
  }

  return { valid: true, message: 'Formato da API Key vlido' };
};

// Funo para obter o melhor modelo para uma tarefa especfica
export const getBestModelForTask = (provider, task) => {
  const providerModels = AI_PROVIDERS[provider]?.models;
  if (!providerModels) return null;

  const models = Object.keys(providerModels);
  
  switch (task) {
    case 'creative_writing':
      // Priorizar modelos mais criativos
      if (provider === 'openai') return 'gpt-4o';
      if (provider === 'anthropic') return 'claude-3-5-sonnet-20241022';
      if (provider === 'google') return 'gemini-1.5-pro';
      return 'llama3-70b-8192';
      
    case 'fast_generation':
      // Priorizar velocidade
      if (provider === 'openai') return 'gpt-3.5-turbo';
      if (provider === 'anthropic') return 'claude-3-haiku-20240307';
      if (provider === 'google') return 'gemini-1.5-flash';
      return 'llama3-8b-8192';
      
    case 'long_context':
      // Priorizar contexto longo
      if (provider === 'anthropic') return 'claude-3-5-sonnet-20241022';
      if (provider === 'google') return 'gemini-1.5-pro';
      if (provider === 'groq') return 'mixtral-8x7b-32768';
      return 'gpt-4-turbo';
    default:
      break;
  }
  
  // Retornar o primeiro modelo disponvel como fallback
  return models[0];
};

export default AIService;
